body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
    text-align: center;
}
h1 {
    margin:0;
    padding: 10px;
}
::-moz-selection {
    color:#FF5252;
}
::selection {
    color:#FF5252;
}
.center-align {
    text-align: center;
}
header {
    height: 100%;
    background: #1976D2;
    text-align: center;
    text-transform: capitalize;
    -webkit-text-decoration: underline black;
            text-decoration: underline black;
    color:white;
}
/* 
    Table CSS
*/
table {
    min-width: 100%;
    margin: 0 auto;
}
tr,td {
    height: 10px;
}
table th {
    background:#1976D2;
    color:white;
    padding: 10px 0px;
}
table tr:nth-child(even){
    background-color: #1976D2;
    color:white;
}
/* 
    Input
*/

.Form {
    margin: 0 auto;
    margin-top: 20px;
    margin-bottom: 20px;
    width: 60%;
    padding: 20px ;
    background: #1976D2;
    color:white;
}

input {
    width: 100%;
    padding: 10px;
    box-sizing: border-box;
}

